.hero__background {
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  width: 100%;
  background-image: url(../app/images/content/backgrounds/homepage_hero.jpg);
  margin: 0;
  padding: 0;
  height: 1000px;
  background-position: center;
}
.hero__background::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  background: linear-gradient(180deg, #111 23%, hsla(0deg, 0%, 7%, 0) 52%);
  width: 100%;
  height: 100%;
  opacity: 0.65;
}
.hero__background::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 70px;
  background: url(./../app/images/content/backgrounds/hero_paperbottom.png);
  background-repeat: 0;
  background-position: bottom;
}

.hero {
  width: 100%;
  height: 700px;
  position: relative;
}
.hero .hero__inner {
  width: 100%;
  max-width: 500px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  color: #ffffff;
}
.hero .hero__inner small {
  color: #FCDC17;
}
.hero .hero__inner h1 {
  font-size: 52px;
  font-weight: 700;
}
.hero .hero__inner .button {
  margin: auto;
}